.galeria{
    display: grid;
    margin-bottom: 5rem;
    @include tablet{
        margin-bottom: 9rem;
        grid-template-columns: repeat(4,1fr);
    }
    .galerimg{
        
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        height: 33rem;
        transition: filter 400ms;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        position: relative;
        

        &:hover ~ h4{
            display: none;
        }

        h4{
            font-size: 8rem;
            color: $magentaOs;
            text-transform: uppercase;
            margin: 0;
            padding: 0;
            cursor: pointer;
            max-width: 21rem;
            z-index: 1;
        }
    }

    .bogota{
        position: relative;
        .bogota__img{
            background-image: url('../img/m4.webp');
        }
    }
    .cali{
        position: relative;
        .cali__img{
            background-image: url('../img/m5.webp');
        }
    }
    .cartagena{
        position: relative;
        .cartagena__img{
            background-image: url('../img/m6.webp');
        }
    }
    .medellin{
        position: relative;
        .medellin__img{
            background-image: url('../img/m7.webp');
        }
    }
    .city{
        position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            filter: grayscale(100%);
            &:hover{
                filter: grayscale(0%);
                cursor: pointer;
            }
    }
}